/* You can add global styles to this file, and also import other style files */
* {
  -webkit-box-sizing: border-box;
  /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;
  /* Firefox, other Gecko */
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  color: #333; }

body {
  font-size: 14px; }

input[type='number'] {
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

.form-group {
  margin: 5px 0; }

.form-group.small {
  margin: 5px 0;
  min-height: 10px; }

.form-group input[type=date],
.form-group input[type=datetime],
.form-group input[type=datetime-local],
.form-group input[type=email],
.form-group input[type=month],
.form-group input[type=number],
.form-group input[type=password],
.form-group input[type=range],
.form-group input[type=search],
.form-group input[type=tel],
.form-group input[type=text],
.form-group input[type=time],
.form-group input[type=url],
.form-group input[type=week] {
  border: 0;
  border-bottom: #555 1px solid;
  padding: 2px 5px;
  width: 100%;
  height: 20px; }
  .form-group input[type=date].small,
  .form-group input[type=datetime].small,
  .form-group input[type=datetime-local].small,
  .form-group input[type=email].small,
  .form-group input[type=month].small,
  .form-group input[type=number].small,
  .form-group input[type=password].small,
  .form-group input[type=range].small,
  .form-group input[type=search].small,
  .form-group input[type=tel].small,
  .form-group input[type=text].small,
  .form-group input[type=time].small,
  .form-group input[type=url].small,
  .form-group input[type=week].small {
    width: 40px; }
  .form-group input[type=date]:focus,
  .form-group input[type=datetime]:focus,
  .form-group input[type=datetime-local]:focus,
  .form-group input[type=email]:focus,
  .form-group input[type=month]:focus,
  .form-group input[type=number]:focus,
  .form-group input[type=password]:focus,
  .form-group input[type=range]:focus,
  .form-group input[type=search]:focus,
  .form-group input[type=tel]:focus,
  .form-group input[type=text]:focus,
  .form-group input[type=time]:focus,
  .form-group input[type=url]:focus,
  .form-group input[type=week]:focus {
    outline: none;
    border-bottom: 1px solid #0078e7;
    background: #e7f3ff; }
  .form-group input[type=date].ng-invalid,
  .form-group input[type=datetime].ng-invalid,
  .form-group input[type=datetime-local].ng-invalid,
  .form-group input[type=email].ng-invalid,
  .form-group input[type=month].ng-invalid,
  .form-group input[type=number].ng-invalid,
  .form-group input[type=password].ng-invalid,
  .form-group input[type=range].ng-invalid,
  .form-group input[type=search].ng-invalid,
  .form-group input[type=tel].ng-invalid,
  .form-group input[type=text].ng-invalid,
  .form-group input[type=time].ng-invalid,
  .form-group input[type=url].ng-invalid,
  .form-group input[type=week].ng-invalid {
    border-bottom: 1px solid red !important;
    background-color: lightpink; }

input.std-width {
  width: 160px; }

.form-group input[type=number] {
  text-align: right; }

.form-group select.std {
  width: 170px; }

.form-group select.small {
  width: 71px; }

select {
  height: 20px;
  border: 0;
  border-bottom: #555 1px solid;
  padding: 1px 2px; }
  select:focus {
    outline: none;
    border-bottom: 1px solid #0078e7;
    background: #e7f3ff; }

select:disabled {
  background-color: #ebebe4; }

select::-ms-expand {
  display: none; }

input[type=checkbox] {
  vertical-align: middle;
  height: 20px; }

input[type='radio'] {
  vertical-align: middle; }

.form-group label {
  min-width: 100px;
  margin-right: 10px;
  font-weight: bold;
  margin-bottom: 0px;
  font-size: 12px;
  display: block; }

.inline-label {
  font-weight: bold;
  font-size: 12px;
  line-height: 20px;
  margin-right: 7px; }

.btn {
  min-width: 50px;
  text-align: center;
  font-family: 'Roboto';
  letter-spacing: 1px;
  border-radius: 0;
  background: white;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  padding: 2px 8px;
  border: 0;
  line-height: 13px;
  padding: 6px 8px;
  vertical-align: baseline; }
  .btn:hover {
    background: #e7f3ff;
    color: #00294e; }

.btn-primary {
  color: white;
  background: #0078e7;
  box-shadow: 1px 1px 3px  #888888; }
  .btn-primary:hover {
    color: #e7f3ff;
    background: #00294e; }

.btn-secondary {
  border: 1px #0078e7 solid;
  color: #0078e7; }
  .btn-secondary:hover {
    color: white;
    background: #0078e7; }

.container {
  padding: 10px;
  height: 100%;
  position: absolute; }

.pull-down {
  padding: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%; }

hr {
  border: 0;
  border-top: 1px solid #aaa; }

.cdk-drag-preview .hide-on-drag {
  display: none !important; }

.node-content-wrapper, .tree-children {
  position: relative; }

.node-content-wrapper::before, .tree-children::after {
  content: "";
  position: absolute; }

.node-content-wrapper::before {
  border-bottom: 1px solid lightgrey;
  border-left: 1px solid lightgrey;
  height: 28px;
  top: -17px;
  width: 20px;
  left: -28px; }

.tree-node-level-1 > tree-node-wrapper > .node-wrapper > .node-content-wrapper::before {
  display: none; }

.tree-node-leaf > .node-wrapper > .node-content-wrapper::before {
  width: 25px; }

.tree-children::after {
  border-left: 1px solid lightgrey;
  height: 100%;
  top: -15px;
  left: -15px; }

tree-node:last-child > .tree-node > .tree-children::after {
  border-left: none; }

.toggle-children {
  z-index: 1; }

[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px; }

[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 23px;
  cursor: pointer;
  line-height: 17px;
  display: inline-block;
  color: #333;
  letter-spacing: 1px;
  min-width: 0px;
  margin-right: 10px;
  text-transform: uppercase; }

.detect-type [type="radio"]:checked + label,
.detect-type [type="radio"]:not(:checked) + label {
  padding-left: 0; }

.detect-type [type="radio"]:checked + label:before,
.detect-type [type="radio"]:not(:checked) + label:before {
  display: none; }

.detect-type [type="radio"]:checked + label:after,
.detect-type [type="radio"]:not(:checked) + label:after {
  display: none; }

.detect-type i {
  font-size: 150%; }

.detect-type [type="radio"]:checked + label i {
  color: #0078e7; }

[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 13px;
  height: 13px;
  border: 1px solid #ddd;
  border-radius: 100%;
  background: #fff; }

[type="radio"]:checked + label:before {
  border: 1px solid #0078e7; }

[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
  content: '';
  width: 9px;
  height: 9px;
  background: #0078e7;
  position: absolute;
  top: 3px;
  left: 3px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0); }

[type="radio"]:checked + label:after {
  color: #0078e7;
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1); }

.ngx-contextmenu {
  background-color: #f3f3f3;
  box-shadow: 2px 2px 4px #aaa; }

.ngx-contextmenu ul {
  margin: 0; }

.ngx-contextmenu .dropdown-menu {
  padding: 0; }

.ngx-contextmenu li {
  min-width: 200px;
  display: block;
  padding: 0;
  color: #333;
  font-weight: bold; }

.ngx-contextmenu li.disabled {
  color: #999; }

.ngx-contextmenu li:first-child {
  border-top: none; }

.ngx-contextmenu a {
  padding: 5px 20px;
  color: #333;
  display: block;
  text-align: left;
  text-decoration: none; }

.ngx-contextmenu li.disabled a {
  color: #999; }

.ngx-contextmenu li:hover {
  background-color: #ccc; }

.ngx-contextmenu li.disabled:hover {
  background-color: #f3f3f3; }

.ngx-contextmenu .key-shortcut {
  float: right;
  font-size: 80%;
  font-weight: normal; }

li.cdk-drag-preview div.interaction {
  display: none !important; }

.distribute {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }
